.education-container {
  min-height: calc(100vh - 273px);

  .title {
    color: #4a4a4a;
    font-size: 21px;
    text-decoration: underline;
    margin-top: 20px;
    margin-bottom: 16px;
    margin-left: 32px;
}

.list-container {
  margin-right: 24px;

  .list {
    border: 1px solid rgba($color: #000000, $alpha: 0.1);
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 15px;
    display: flex;
    text-align: end;

    .image {
      width: 50px;
      height: 50px;
    }
  }
}

.image-education {
  margin-left: 27px;
  margin-bottom: 20px;

  @media (min-width:540px){
    margin-left: 30%;
   }
}
  }


  
