.experience-container {
  min-height: calc(100vh - 273px);

  .title {
    color: #4a4a4a;
    font-size: 21px;
    text-decoration: underline;
    margin-top: 18px;
    margin-bottom: 20px;
  }

  .list {
    border: 1px solid rgba($color: #000000, $alpha: 0.1);
    border-radius: 5px;
    padding-bottom: 5px;

    .paragraph {
      margin-top: 10px;
    }
  }

  .imgage-experience {
    margin-bottom: 20px;
    margin-left: 5px;
    border-radius: 8px;
    box-shadow: 0 8px 8px 0 #773da1;

    @media (min-width:540px){
      margin-left: 27%;
     }
  }
}
