.projects-container {
  min-height: calc(100vh - 273px);

    .project-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .image-projects {
        width: 123px;
        height: 129px;
        border-radius: 25px;
        margin-top: 16px;
        box-shadow: 0 8px 8px 0 #773da1;
      }
  
      .description {
        margin: 14px 16px 0px 25px;
        padding: 5px;
        text-align: justify;
      }
    }  
  }

